import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  message,
  Button
} from 'antd'
import {CsxFormSelect} from 'Form'
import {API_DICTIONARYS, API_SYSTEMCONFIGS_RESERVOIRAREACONFIGS_PAGE, API_RESERVOIRAREAS_WAREHOUSETYPES} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.reservoirArea, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
  }
  componentDidMount() {
    // 数据初始化
    const {modalData, update, userLoation} = this.props
    // 判断是新增还是编辑
    if (modalData) {
      let reqObj = {...modalData}
      // 特殊处理数据
      reqObj.reservoirAreaTypeCode = {
        children: reqObj.reservoirAreaTypeName && reqObj.reservoirAreaTypeName.toString(),
        value: reqObj.reservoirAreaTypeCode && reqObj.reservoirAreaTypeCode.toString()
      }
      reqObj.warehouseCode = {
        children: reqObj.warehouseName && reqObj.warehouseName.toString(),
        value: reqObj.warehouseCode && reqObj.warehouseCode.toString()
      }
      reqObj.reservoirAreaCode = {
        children: reqObj.reservoirAreaName && reqObj.reservoirAreaName.toString(),
        value: reqObj.reservoirAreaCode && reqObj.reservoirAreaCode.toString()
      }
      reqObj.reservoirAreaAttribute = {
        children: reqObj.reservoirAreaAttributeDesc && reqObj.reservoirAreaAttributeDesc.toString(),
        value: reqObj.reservoirAreaAttribute && reqObj.reservoirAreaAttribute.toString()
      }
      update({modalData: {...reqObj}})
    } else {
      let warehouseCode = {
          value: userLoation.value,
          children: userLoation.children
        },
        reservoirAreaTypeCode = {
          value: 'BZ',
          children: '标准区'
        },
        reservoirAreaAttribute = {
          value: 'C',
          children: '成品'
        }
      if (userLoation.type == 1 || userLoation.type == 3) {
        update({modalData: {warehouseCode, reservoirAreaTypeCode, reservoirAreaAttribute}})
      } else {
        update({modalData: {warehouseCode, reservoirAreaTypeCode}})
      }
    }
  }
  handleSubmit = (e) => {
    const {form, modalData, handleAddOrEdit} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {...values}
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'reservoirAreaTypeCode') {
              reqObj.reservoirAreaTypeName = reqObj[i].children
            }
            if (i == 'warehouseCode') {
              reqObj.warehouseName = reqObj[i].children
            }
            if (i == 'reservoirAreaCode') {
              reqObj.reservoirAreaName = reqObj[i].children
            }
            reqObj[i] = reqObj[i].value
          }
        }

        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  render() {
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData, userLoation} = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16}
      }
    }
    console.log(userLoation)
    return (
      <Modal
        title={modalTitle == 1 ? '新增库区' : modalTitle == 2 ? '编辑库区' : ''}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='900px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="仓库"
              >
                {getFieldDecorator('warehouseCode', {
                  initialValue: modalData.warehouseCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_DICTIONARYS}
                    searchParams={{dicTypes: 'WAREHOUSETYPE'}}
                    antd={{disabled: true}}
                    selectItem={{key: 'dicKey', valueKey: ['dicKey', 'dicValue']}}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="库区名称"
                hasFeedback
              >
                {getFieldDecorator('reservoirAreaCode', {
                  initialValue: modalData.reservoirAreaCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_SYSTEMCONFIGS_RESERVOIRAREACONFIGS_PAGE}
                    selectItem={{key: 'sysCode', valueKey: 'sysValue'}}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="库区类型"
              >
                {getFieldDecorator('reservoirAreaTypeCode', {
                  initialValue: modalData.reservoirAreaTypeCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_DICTIONARYS}
                    searchParams={{dicTypes: 'DATYPE'}}
                    selectItem={{key: 'dicKey', valueKey: 'dicValue'}}
                    antd={{disabled: true}}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="库区属性"
                hasFeedback
              >
                {getFieldDecorator('reservoirAreaAttribute', {
                  initialValue: modalData.reservoirAreaAttribute,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_RESERVOIRAREAS_WAREHOUSETYPES}
                    searchParams={{type: 'A'}}
                    selectItem={{key: 'key', valueKey: 'value'}}
                    antd={{disabled: modalTitle == 2 ? true : false}}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
         <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



