import * as actionType from './actionType'
import {getData} from 'util'
import {API_RESERVOIRAREAS_PAGE, API_RESERVOIRAREAS_CREATE, API_RESERVOIRAREAS_UPDATE, API_RESERVOIRAREAS_UPDATESTATUS, API_PRODUCT_INIT} from 'api'
//同步
export const update = (object) => ({
  type: actionType.RESERVOIRAREA_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.RESERVOIRAREA_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_RESERVOIRAREAS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.RESERVOIRAREA_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.RESERVOIRAREA_FILE
    })
  })
}

// 创建/编辑仓库
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.RESERVOIRAREA_UPDATE, data: {modalTuttonLoading: true}})
  let api = reqData.data && reqData.data.id ? API_RESERVOIRAREAS_UPDATE : API_RESERVOIRAREAS_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.RESERVOIRAREA_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.RESERVOIRAREA_FILE
    })
  })
}

// 启用禁用仓库
export const handleOperator = (reqData, callBack) => (dispatch) => {
  getData({api: API_RESERVOIRAREAS_UPDATESTATUS, ...reqData}).then(data => {
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.RESERVOIRAREA_FILE
    })
  })
}

// 初始化
export const handleInit = (reqData, callBack) => (dispatch) => {
  getData({api: API_PRODUCT_INIT, ...reqData}).then(data => {
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.RESERVOIRAREA_FILE
    })
  })
}


