import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.reservoirArea, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 150,
      dataIndex: 'reservoirStatus',
      key: 'reservoirStatus',
      render: (text, record, index) => <div className="tableOperation">
        <span className='blue' onClick={() => {
          this.onEdit(record, 2)
        }}>编辑</span>
        {/* {
          text ? <span onClick={() => {
            this.handleOperator(text, record, index, 0)
          }}>禁用</span> : <span onClick={() => {
            this.handleOperator(text, record, index, 1)
          }}>启用</span>
        } */}
      </div>
    }, {
      title: '仓库编号',
      width: 150,
      dataIndex: 'warehouseCode',
      key: 'warehouseCode'
    }, {
      title: '库区编号',
      width: 150,
      dataIndex: 'reservoirAreaCode',
      key: 'reservoirAreaCode'
    }, {
      title: '库区名称',
      width: 150,
      dataIndex: 'reservoirAreaName',
      key: 'reservoirAreaName'
    }, {
      title: '库区类型',
      width: 150,
      dataIndex: 'reservoirAreaTypeName',
      key: 'reservoirAreaTypeName'
    }, {
      title: '默认库区',
      width: 150,
      dataIndex: 'defaultReservoirFlagDesc',
      key: 'defaultReservoirFlagDesc'
    }, {
      title: '默认商品储位',
      width: 150,
      dataIndex: 'defaultProductLocation',
      key: 'defaultProductLocation'
    }, {
      title: '默认退货储位',
      width: 150,
      dataIndex: 'defaultReturnProductLocation',
      key: 'defaultReturnProductLocation'
    }, {
      title: '默认越库位',
      width: 150,
      dataIndex: 'defaultCrossDockingLocation',
      key: 'defaultCrossDockingLocation'
    }]
  }

  // 初始化
  handleInit(text, record, index) {
    const {handleGetList, searchParams, page, size, handleInit} = this.props
    handleInit({
      data: record
    }, () => {
      message.success('初始化成功')
      handleGetList({searchParams: {page, size, ...searchParams}})
    })
  }

  handleOperator(text, record, index, type) {
    const {handleGetList, searchParams, page, size, handleOperator} = this.props
    confirm({
      title: '温馨提示',
      content: `是否${type ? '启用' : '禁用'}该条信息？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator({
          data: {
            id: record.id,
            reservoirStatus: type
          },
          urlParams: [record.id]
        }, () => {
          message.success('操作成功')
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  onEdit(record, titleType) {
    const {update} = this.props
    update({isShowModal: true, modalTitle: titleType, modalData: record})
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: true, columns: sorter.field}})
      } else {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: false, columns: sorter.field}})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...searchParams}})
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    console.log(this.props)
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 1350, y: 400}}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



