import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect} from 'Form'
import {API_STORAGELOCATIONS_RESERVOIRAREAS} from 'api'

@connect(
  state => ({...state.reservoirArea}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 新增
  handleAdd = (titleType) => {
    const {update} = this.props
    update({isShowModal: true, modalTitle: titleType, modalData: ''})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }
  render() {
    const {
      searchParams: {reservoirAreaCode, reservoirAreaName}
    } = this.props
    console.log(this.props.searchParams, 111)
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <CsxSelect
              dateApi={API_STORAGELOCATIONS_RESERVOIRAREAS}
              label='库区编号'
              selectItem={{data: reservoirAreaCode, key: 'key', valueKey: 'key'}}
              onChangeSelect={(value) => {
                this.onInputChange({reservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>库区名称：</label>
              <div className='box'>
                <Input value={reservoirAreaName} onChange={(e) => {
                  this.onInputChange({reservoirAreaName: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={12}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
              <Button type="primary" onClick={() => {
                this.handleAdd(1)
              }}>新增</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



